<template>
  <div class="log-page">
    <div class="log-item" v-for="(item, index) in list" :key="index">
      <div class="log-version">
        <span class="log-name">v{{ item.version }}</span>
        <span class="log-date">{{ item.date }}</span>
        <a v-if="proComp && index < 3" class="link download-lib" @click="downloadEvent(item.version)">点击下载</a>
      </div>
      <div class="log-desc">
        <ul class="log-list">
          <li class="log-li" v-for="(log, cIndex) in item.logs" :key="`${index}${cIndex}`">{{ log }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { downloadLib } from './utils'

export default {
  data () {
    return {
      list: [
        {
          version: '1.0.14',
          date: '2020-12-18',
          logs: [
            '修复浏览器缩放导致单元格选取偏移问题'
          ]
        },
        {
          version: '1.0.13',
          date: '2020-12-17',
          logs: [
            '支持参数 keyboard-config.delMethod',
            '支持参数 keyboard-config.backMethod'
          ]
        },
        {
          version: '1.0.12',
          date: '2020-12-15',
          logs: [
            '修复虚拟滚动序号复制不正确的问题',
            '修复数据重新加载后快捷键全选报错问题'
          ]
        },
        {
          version: '1.0.11',
          date: '2020-12-12',
          logs: [
            '优化方向键扩展区域',
            '支持 Shift+鼠标 点选区域功能'
          ]
        },
        {
          version: '1.0.10',
          date: '2020-12-09',
          logs: [
            '修复查找与替换输入框在 v3 中无法输入问题'
          ]
        },
        {
          version: '1.0.9',
          date: '2020-12-09',
          logs: [
            '修复粘贴 Excel 数据错误问题',
            '修复单元格格式化值无法复制问题',
            '修复序号、复选框、单选框无法复制与粘贴问题'
          ]
        },
        {
          version: '1.0.8',
          date: '2020-12-07',
          logs: [
            '修复筛选弹出后复制与粘贴冲突问题'
          ]
        },
        {
          version: '1.0.7',
          date: '2020-12-06',
          logs: [
            '修复点击列头选取整列后未定位到第一行问题'
          ]
        },
        {
          version: '1.0.6',
          date: '2020-12-01',
          logs: [
            '增加参数 clip-config.copyMethod 对应旧的 clip-config.getMethod',
            '增加参数 clip-config.beforeCopyMethod 对应旧的 clip-config.beforeGetMethod',
            '增加参数 clip-config.pasteMethod 对应旧的 clip-config.setMethod',
            '增加参数 clip-config.beforePasteMethod 对应旧的 clip-config.beforeSetMethod'
          ]
        },
        {
          version: '1.0.5',
          date: '2020-11-29',
          logs: [
            '优化 cell-area-* 相关事件的参数'
          ]
        },
        {
          version: '1.0.4',
          date: '2020-11-25',
          logs: [
            '支持查找与替换可自定义显示',
            '支持查找与替换自定义方法'
          ]
        },
        {
          version: '1.0.3',
          date: '2020-11-23',
          logs: [
            '修复 MacOS 复制粘贴失效问题'
          ]
        },
        {
          version: '1.0.2',
          date: '2020-11-10',
          logs: [
            '增加参数 clip-config',
            '增加事件 cell-area-copy',
            '增加事件 cell-area-cut',
            '增加事件 cell-area-paste',
            '增加事件 cell-area-merge',
            '增加事件 cell-area-selection-start',
            '增加事件 cell-area-selection-end',
            '增加事件 cell-area-extension-start',
            '增加事件 cell-area-extension-end'
          ]
        },
        {
          version: '1.0.1',
          date: '2019-10-01',
          logs: [
            '内部优化',
            '小bug修复'
          ]
        },
        {
          version: '1.0.0',
          date: '2019-06-01',
          logs: [
            '实现对单元格操作的完整支持'
          ]
        }
      ]
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo'
    ]),
    proComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList) {
        return this.loginInfo.compList.find(comp => comp.appCode === 'PRO')
      }
      return null
    }
  },
  methods: {
    downloadEvent (version) {
      downloadLib('PRO', version)
    }
  }
}
</script>

<style lang="scss" scoped>
.log-page {
  width: 800px;
  margin: 0 auto;
  padding: 0 10px;
}
.log-name {
  font-size: 22px;
  font-weight: 700;
  color: #0d1a26;
}
.log-date {
  font-size: 12px;
  margin-left: 15px;
  padding: 2px 5px;
  background: #f2f4f5;
  border: 1px solid #eee;
}
.log-list {
  .log-li {
    list-style-type: circle;
  }
}
</style>
