import store from '../store'
import pako from 'pako'
import XEAjax from 'xe-ajax'
import XEUtils from 'xe-utils'

const invalidCode = []

export function compressStr (str) {
  return pako.gzip(XEUtils.toString(str), { to: 'string' })
}

export function compressObj (obj) {
  return pako.gzip(JSON.stringify(obj), { to: 'string' })
}

export function getRest (data) {
  if (XEUtils.isObject(data[0]) && data[0]) {
    return XEUtils.toStringJSON(pako.ungzip(data[0], { to: 'string' }))
  }
  return data
}

export function downloadLib (compName, version) {
  const loginToken = store.state.loginToken
  if (loginToken) {
    if (!invalidCode.includes(`${compName}_${version}_${loginToken}`)) {
      const strs = 'wabpcdoefghijklmnzyt'.split('')
      const params = {}
      XEUtils.sample(strs, 6).forEach(key => {
        params[key] = XEUtils.random(10, 999)
      })
      XEAjax.fetchPost(`/api/pub/vxetable/download/${compName}_${encodeURIComponent(version)}/${encodeURIComponent(loginToken)}`, {
        n: Date.now(),
        l: compName,
        t: loginToken.slice(30, 80)
      }, { params }).then(response => {
        if (response.status === 200) {
          response.blob().then(blob => {
            const a = document.createElement('a')
            a.target = '_blank'
            a.download = `${compName}_v${version}.rar`
            a.href = URL.createObjectURL(blob)
            document.body.appendChild(a)
            a.click()
            document.body.removeChild(a)
          })
        } else {
          response.json().then(data => {
            const rest = getRest(data)
            if (rest) {
              if (rest.code > 10) {
                invalidCode.push(`${compName}_${version}_${loginToken}`)
              }
              alert(rest.message)
            } else {
              alert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
            }
          })
        }
      })
    } else {
      alert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
    }
  } else {
    alert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
  }
}
