import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    loginToken: sessionStorage.getItem('AUTH_TOKEN'),
    loginInfo: null
  },
  mutations: {
    setLoginToken (state, token) {
      state.loginToken = token
    },
    setLoginInfo (state, obj) {
      state.loginInfo = obj
    }
  },
  actions: {
  },
  modules: {
  }
})

export default store
